#about {
  margin-top: 150px; }
  #about .custom {
    padding: 0 !important;
    margin: 0 !important; }
  #about .aboutus {
    height: 100vh;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
    background-image: url("http://outspoke.co/wp-content/uploads/2016/04/Outspoke-About-Us-Background.jpg"); }
    #about .aboutus h1 {
      padding-top: 285px;
      display: flex;
      justify-content: center;
      overflow-x: hidden;
      color: white; }
    #about .aboutus .about-title {
      font-family: Oswald;
      text-align: center;
      font-size: 350%; }
  #about .joine {
    max-width: 700px;
    margin: -25px auto;
    margin-bottom: 25px; }
    #about .joine .about-text {
      overflow-y: hidden;
      color: white;
      font-family: 'SegoeUIRegular';
      padding: 0 40px;
      text-align: center; }
  #about .about-features {
    overflow: auto;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("http://localhost:8000/src/assets/blurred-background.jpg");
    height: auto;
    text-align: center; }
    #about .about-features .about-section {
      padding: 50px 0;
      color: #fff; }
  #about .about-toggle {
    height: auto;
    /* Hover effect style */
    /* Effects as a button */ }
    #about .about-toggle a {
      outline: none;
      color: #3498db;
      text-decoration: none; }
    #about .about-toggle a:hover, #about .about-toggle a:focus {
      color: #528cb3; }
    #about .about-toggle figure {
      position: relative;
      overflow: hidden;
      height: auto;
      background: #3085a3;
      text-align: center;
      cursor: pointer; }
    #about .about-toggle figure figcaption {
      color: #fff;
      text-transform: uppercase;
      font-size: 1.25em;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden; }
    #about .about-toggle figure figcaption::before,
    #about .about-toggle figure figcaption::after {
      pointer-events: none; }
    #about .about-toggle figure figcaption,
    #about .about-toggle figure figcaption > a {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden; }
    #about .about-toggle figure figcaption > a {
      z-index: 1000;
      text-indent: 200%;
      white-space: nowrap;
      font-size: 0;
      opacity: 0; }
    #about .about-toggle figure h2 {
      word-spacing: -0.15em;
      font-weight: 300; }
    #about .about-toggle figure h2 span {
      font-weight: 800; }
    #about .about-toggle figure h2,
    #about .about-toggle figure p {
      margin: 0; }
    #about .about-toggle figure p {
      letter-spacing: 1px;
      font-size: 68.5%; }
    #about .about-toggle figure.about-toggle-item figcaption::after,
    #about .about-toggle figure.about-toggle-item p {
      -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
      transition: opacity 0.35s, transform 0.35s; }
    #about .about-toggle figure.about-toggle-item figcaption::after {
      overflow: hidden !important;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-top: 1px solid #fff;
      border-bottom: 1px solid #fff;
      content: '';
      opacity: 0;
      -webkit-transform: rotate3d(0, 0, 1, 45deg) scale3d(1, 0, 1);
      transform: rotate3d(0, 0, 1, 45deg) scale3d(1, 0, 1);
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%; }
    #about .about-toggle figure.about-toggle-item h2,
    #about .about-toggle figure.about-toggle-item p {
      opacity: 1;
      -webkit-transform: scale3d(0.8, 0.8, 1);
      transform: scale3d(0.8, 0.8, 1); }
    #about .about-toggle figure.about-toggle-item h2 {
      padding-top: 26%;
      -webkit-transition: -webkit-transform 0.35s;
      transition: transform 0.35s; }
    #about .about-toggle figure.about-toggle-item p {
      padding: 0.5em 2em;
      text-transform: none;
      font-size: 0.85em;
      opacity: 0; }
    #about .about-toggle figure.about-toggle-item:hover figcaption::after {
      opacity: 1;
      -webkit-transform: rotate3d(0, 0, 1, 45deg) scale3d(1, 1, 1);
      transform: rotate3d(0, 0, 1, 45deg) scale3d(1, 1, 1); }
    #about .about-toggle figure.about-toggle-item:hover h2,
    #about .about-toggle figure.about-toggle-item:hover p {
      opacity: 1;
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); }
    #about .about-toggle .about-toggle-item {
      padding: 0;
      height: 50vh;
      background-repeat: no-repeat;
      background-size: cover; }
    #about .about-toggle .toggle-discover, #about .about-toggle .toggle-contact {
      background-position: center center;
      bacground-size: cover;
      background-repeat: no-repeat; }
    #about .about-toggle .toggle-discover {
      background-image: url("http://localhost:8000/src/assets/people-crowd-event-amusement-park.jpeg"); }
    #about .about-toggle .toggle-contact {
      background-image: url("http://localhost:8000/src/assets/person-woman-hand-smartphone.jpg"); }
