.snip {
  font-family: 'Open Sans', Arial, sans-serif;
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 150px;
  max-width: 310px;
  width: 100%;
  background-color: #ffffff;
  color: #000000;
  text-align: left;
  font-size: 16px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15); }

.snip * {
  box-sizing: border-box; }

.snip:after {
  position: absolute;
  top: 12px;
  left: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 25px 0 25px 25px;
  border-color: transparent transparent transparent #FC575E;
  content: '';
  transform: translateX(-100%);
  transition: all 0.3s ease; }

.snip img {
  margin: 0;
  max-width: 100%;
  vertical-align: top;
  position: relative; }

.snip figcaption {
  padding: 20px 20px 20px;
  background-color: #ffffff; }

.snip .date {
  background-color: #FC575E;
  top: 15px;
  color: #fff;
  right: 15px;
  min-height: 48px;
  min-width: 48px;
  position: absolute;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 50%;
  padding: 10px 0; }

figure .date:hover,
figure.snip.hover .date {
  -webkit-transform: rotateY(90deg);
  transform: rotateY(90deg);
  -webkit-transition-delay: 0s;
  transition-delay: 0s; }

figure.snip:hover i,
figure.snip.hover i {
  -webkit-transform: rotateY(0);
  transform: rotateY(0);
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s; }

figure.snip:hover button,
figure.snip.hover button {
  background-color: #117964; }

.snip .date span {
  display: block;
  line-height: 14px; }

.snip .date .month {
  font-size: 11px; }

.snip h3,
.snip p {
  margin: 0;
  padding: 0; }

.snip h3 {
  margin-bottom: 10px;
  display: inline-block;
  font-weight: 700; }

.snip p {
  font-size: 0.8em;
  margin-bottom: 10px;
  line-height: 1.6em; }

.snip a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1; }

.snip:hover:after,
.snip.hover:after {
  transform: translateX(0%); }

.created {
  position: relative;
  margin-top: 6%; }

.figures {
  overflow-y: hidden;
  display: flex;
  justify-content: center; }

