#event {
  margin-top: 150px; }

@media screen and (max-width: 425px) {
  .author-area {
    display: none; }
  .subscribe {
    float: none;
    display: block;
    margin-top: 15px;
    padding-left: 0;
    margin-left: 25px; }
  .info-event {
    margin-top: 25px; } }

@media screen and (min-width: 425px) {
  .subscribe {
    margin-top: -25px;
    float: right; }
  .info-event {
    margin-top: 70px; } }

.event-heading {
  text-align: center;
  font-family: Oswald;
  font-size: 54px;
  padding-top: 25px;
  padding-bottom: 25px; }

.event_photo {
  padding: 10px; }
  .event_photo img {
    width: 750px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }

.ev-description {
  margin-left: 25px;
  display: block;
  color: white;
  background-color: #373746;
  resize: none; }

.picture-wrap {
  color: #253939;
  border: 2px dashed #253939;
  border-radius: 10px;
  text-align: center;
  height: 400px;
  position: relative; }
  .picture-wrap .event-picture {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat; }

.event_description_social {
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

h2 {
  margin: 0.5rem 0 1rem; }

.member_wrapper {
  display: flex;
  flex-direction: row; }

.event_member img {
  padding: 10px 15px;
  width: 150px; }

.event_info h1 {
  text-align: center; }

.event_info .event_weather h2 {
  text-align: center; }

.events_similar, .event-members, .candidats {
  color: #373746;
  font-family: Oswald;
  margin-left: 25px; }

.event_info_name {
  text-align: center; }

.event_chr {
  border-bottom: 1px solid black;
  max-width: 650px;
  padding: 15px 0; }

i {
  border-radius: 50%; }

.fa {
  padding: 15px 20px;
  color: white; }

.icon-zoom .fa:hover, .social-icons.icon-zoom .fa:active {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  transform: scale(1.1); }

.fa-facebook {
  background-color: #3C599F; }

.fa-twitter {
  background-color: #32CCFE; }

.fa-google-plus {
  background-color: #CF3D2E; }

.fa-pinterest {
  background-color: #CC2127; }

.author-area {
  position: absolute;
  margin-top: -170px;
  margin-left: 75px;
  background-color: white;
  border: 1px solid #373746;
  -webkit-box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75); }
  .author-area .event_author {
    width: 200px;
    height: 150px;
    margin: 10px;
    border: 1px solid #373746; }
  .author-area .author-name {
    color: #373746;
    text-align: center;
    font-family: 'SegoeUIRegular';
    font-size: 18px; }

.info-event {
  margin-left: 25px !important; }

.custom-description {
  resize: none;
  border: 1px solid #373746;
  border-top: none;
  border-bottom: none;
  color: white;
  margin-right: 25px;
  margin-bottom: 25px; }

.entry-title {
  font-family: 'SegoeUIRegular';
  margin-top: 190px; }

.separator {
  margin: 10px 25px 10px 25px; }

.edit-btn {
  float: right; }

.weather {
  padding: 0;
  font-family: Oswald; }

.event-properties {
  margin-right: 25px;
  padding: 0; }

.subscribe {
  text-align: center; }

.subscribe-btn {
  padding: 5px 25px;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 16px;
  font-weight: 400;
  line-height: 45px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle; }

.subscribe-btn-effect {
  border: 1px solid;
  overflow: hidden;
  position: relative; }
  .subscribe-btn-effect span {
    z-index: 20; }
  .subscribe-btn-effect:after {
    background: #fff;
    content: "";
    height: 155px;
    left: -75px;
    opacity: .2;
    position: absolute;
    top: -50px;
    transform: rotate(35deg);
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    width: 50px;
    z-index: -10; }

.subscribe-btn-effect:hover:after {
  left: 120%;
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1); }

.candidat {
  margin-left: 25px;
  position: relative;
  display: inline-block;
  -webkit-box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75); }
  .candidat .candidat-photo {
    width: 100px;
    height: 75px; }
  .candidat .reject, .candidat .approve {
    position: absolute;
    top: 25px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    text-align: center;
    color: white;
    -webkit-box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 3px 3px 7px -1px rgba(0, 0, 0, 0.75); }
  .candidat .reject {
    left: -15px;
    background-color: #2ecc71; }
  .candidat .approve {
    right: -15px;
    background-color: #FC575E; }
  .candidat .symb {
    margin-top: 5px; }
